import React from 'react';
import {Transaction} from '../Transaction/Transaction';
import styles from './Transactions.module.css';

export function Transactions({
    userId,
    transactions, 
    headers, 
    showCorrectHeader, 
    removeTransaction,
    changeTransaction, 
    lang, 
    maxItemsOnPage,
    setMaxItemsOnPage,
    setAddTransactionModalOn,
    setEditTransactionModalOn,
    setEditTransactionId,
    setEditDate,
    setEditDescription,
    setEditHeaderId,
    setEditAmount,
    setImportModalOn,
    setFilterModalOn,
    resetFilters}) {

    let itemsOnPage = 0;

    function handleAddTransaction() {
        setAddTransactionModalOn(true);
    }

    function handleImport() {
        setImportModalOn(prev => true);
    }

    function handleFilter() {
        setFilterModalOn(prev => true);
    }

    function handleReset() {
        resetFilters();
    }

    function showMoreItems() {
        setMaxItemsOnPage(prev => prev + 50);
    }

    if (!userId) {
        return <p>You are not logged in</p>;
    }

    return (
        <div className={styles.transactions}>
           <h2 className={styles.title}>{lang === 'en' ? 'Transactions' : 'Transacties'}</h2>
            <p className={styles.showing}>{lang === 'en' ? `Showing ${Math.min(maxItemsOnPage, transactions.length)} of ${transactions.length} transactions` : `${Math.min(maxItemsOnPage, transactions.length)} van ${transactions.length} transacties tonend`}</p>
            <div className={styles.buttons}>
                <button className={styles['button-add-transaction']} onClick={handleAddTransaction}>{lang === 'en' ? 'Add transaction' : 'Transactie toevoegen'}</button>
                <button className={styles['button-import-transactions']} onClick={handleImport}>{lang === 'en' ? 'Import transactions' : 'Transacties importeren'}</button>
                <button className={styles['button-add-filters']} onClick={handleFilter}>{lang === 'en' ? 'Add filters' : 'Filters toevoegen'}</button>
                <button className={styles['button-reset-filters']} onClick={handleReset}>{lang === 'en' ? 'Reset filters' : 'Filters resetten'}</button>
            </div>
            <table>
                <thead>
                    <tr>
                        <th className={styles['column-date']}>{lang === 'en' ? 'Date' : 'Datum'}</th>
                        <th className={styles['column-description']}>{lang === 'en' ? 'Description' : 'Omschrijving' }</th>
                        <th className={styles['column-header']}>{lang === 'en' ? 'Header' : 'Rubriek'}</th>
                        <th className={styles['column-amount']}>{lang === 'en' ? 'Amount' : 'Bedrag'}</th>
                        <th className={styles['column-remove']}></th>
                    </tr>
               </thead>
                <tbody>
                     {transactions.map((transaction, i) => {
                        itemsOnPage++;
                        return itemsOnPage <= maxItemsOnPage && <Transaction
                        key={i}
                        headers={headers}
                        showCorrectHeader={showCorrectHeader}
                        id={transaction.id}
                        date={transaction.date} 
                        description={transaction.description}
                        headerId={transaction.header_id} 
                        amount={transaction.amount} 
                        isIncome={transaction.is_income}
                        removeTransaction={removeTransaction} 
                        changeTransaction={changeTransaction} 
                        setEditTransactionModalOn={setEditTransactionModalOn}
                        setEditTransactionId={setEditTransactionId}
                        lang={lang}
                        setEditDate={setEditDate}
                        setEditDescription={setEditDescription}
                        setEditHeaderId={setEditHeaderId}
                        setEditAmount={setEditAmount}/>
                    })}
                </tbody>
            </table>
                <button className={maxItemsOnPage < transactions.length ? styles['show-more-button-show'] : styles['show-more-button-hide']} onClick={showMoreItems}>{lang === 'en' ? 'Show more' : 'Toon meer'}</button>
        </div>
    );
}