import React, {useState} from 'react';
import {useHistory} from 'react-router-dom';
import styles from './Login.module.css';
import Server from '../../utils/Server'

export function Login({
    setUserId,
    email,
    setEmail,
    password,
    setPassword,
    newEmail,
    setNewEmail,
    newPassword1,
    setNewPassword1,
    newPassword2,
    setNewPassword2,
    forgotPasswordEmail,
    setForgotPasswordEmail}) {
        
    const [status, setStatus] = useState('login');
    const [message, setMessage] = useState({});

    const history = useHistory();

    function handleLogin(e) {
        e.preventDefault();
        Server.login(email, password).then(res => {
            setUserId(res);
            history.push('/transactions');
        }).catch(err => {
            setMessage({message: 'Invalid combination of e-mail and password', error: true});
        })
    }

    function handleRegister(e) {
        e.preventDefault();
        if (newEmail.trim().length < 5 || newEmail.trim().length > 50) {
            setMessage({message: 'E-mail needs to be between 5 and 50 characters', error: true})
        }
        else if (newPassword1 !== newPassword2) {
            setMessage({message: 'Passwords do not match', error: true})
        }
        else if (newPassword1.trim().length < 8 || newPassword1.trim().length > 50) {
            setMessage({message: 'Password needs to be between 8 and 50 characters', error: true})
        }
        else {
            Server.register(newEmail.trim(), newPassword1.trim()).then(res => {
                setMessage({message: `Succesfully registered ${newEmail} as new user`, error: false});
                setNewEmail('');
                setNewPassword1('');
                setNewPassword2('');
            }).catch(err => {
                setMessage({message: 'That address is already registered', error: true});
            })
        }
    }

    function handleForgotPassword(e) {
        e.preventDefault();
        Server.getPassword(forgotPasswordEmail).then(res => {
            setMessage({message: `The password has been sent to ${forgotPasswordEmail}`, error: false});
            setForgotPasswordEmail('');
        }).catch(err => {
            setMessage({message: 'That address is not registered', error: true});
        })
    }
      
    return (
        <div className={styles.login}>
            <h1>XPENSOFT</h1>
            {status === 'login' && <form>
                <h2>Login</h2>
                <br/>
                <label htmlFor='email'>E-mail</label>
                <input data-test='email' type='email' id='email' value={email} onChange={e => setEmail(e.target.value)}/>
                <label htmlFor='password'>Password</label>
                <input type='password' id='password' value={password} onChange={e => setPassword(e.target.value)}/>
                <button className={styles['button-login']} onClick={handleLogin}>Login</button>
                <p className={message.error? styles.error : styles.message}>{message.message}</p>
                <p className={styles['button-forgot-password']} onClick={e => {setMessage({}); setStatus('get-password')}}>Forgot password</p>
                <button className={styles['button-register']} onClick={e => {setMessage({}); setStatus('register')}}>Register</button>
          </form>}
          {status === 'register' && <form>
                <h2>Register</h2>
                <br/>
                <label htmlFor='newemail'>E-mail: </label>
                <input type='email' id='newemail' value={newEmail} onChange={e => setNewEmail(e.target.value)}/>
                <label htmlFor='newpassword1'>Password:</label>
                <input type='password' id='newpassword1' value={newPassword1} onChange={e => setNewPassword1(e.target.value)}/>
                <label htmlFor='newpassword2'>Repeat password:</label>
                <input type='password' id='newpassword2' value={newPassword2} onChange={e => setNewPassword2(e.target.value)}/>
                <p className={message.error? styles.error : styles.message}>{message.message}</p>
               <button onClick={handleRegister}>Register</button>
                <button onClick={e => {setMessage({}); setStatus('login')}}>Cancel</button>
          </form>}
          {status === 'get-password' && <form>
                <h2>Forgot password</h2>
                <br/>
                <label htmlFor='forgotpasswordemail'>E-mail: </label>
                <input type='email' id='forgotpasswordemail' value={forgotPasswordEmail} onChange={e => setForgotPasswordEmail(e.target.value)}/>
                <p className={message.error? styles.error : styles.message}>{message.message}</p>
                <button onClick={handleForgotPassword}>Submit</button>
                <button onClick={e => {setMessage({}); setStatus('login')}}>Cancel</button>
            </form>}
        </div>
    );
}