import React, {useState, useEffect, useRef} from 'react';
import {Switch, Route, Redirect, NavLink, useHistory} from 'react-router-dom';
import {Login} from './components/Login/Login';
import {Transactions} from './components/Transactions/Transactions';
import {Overview} from './components/Overview/Overview';
import Server from './utils/Server';
import {ErrorModal} from './components/ErrorModal/ErrorModal';
import {ImportModal} from './components/ImportModal/ImportModal';
import {FilterModal} from './components/FilterModal/FilterModal'
import {AddEditTransactionModal} from './components/AddEditTransactionModal/AddEditTransactionModal';
import {ChangeViewModal} from './components/ChangeViewModal/ChangeViewModal';
import {AddEditHeaderModal} from './components/AddEditHeaderModal/AddEditHeaderModal';
import styles from './App.module.css';
import imageEN from './images/EN.png';
import imageNL from './images/NL.png';

function App() {

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const [newEmail, setNewEmail] = useState('');
  const [newPassword1, setNewPassword1] = useState('');
  const [newPassword2, setNewPassword2] = useState('');

  const [forgotPasswordEmail, setForgotPasswordEmail] = useState('');
  
  const [userId, setUserId] = useState(0);
  
  //Hold the transactions
  const [transactions, setTransactions] = useState([]);
  
  //Hold the headers
  const [headers, setHeaders] = useState([]);

  //Hold the data for a new transaction
  const [addTransactionModalOn, setAddTransactionModalOn] = useState(false);
  const [date, setDate] = useState('');
  const [description, setDescription] = useState('');
  const [headerId, setHeaderId] = useState('');
  const [amount, setAmount] = useState('');

  //Hold the data for a changed transaction
  const [editTransactionModalOn, setEditTransactionModalOn] = useState(false);
  const [editTransactionId, setEditTransactionId] = useState();
  const [editDate, setEditDate] = useState('');
  const [editDescription, setEditDescription] = useState('');
  const [editHeaderId, setEditHeaderId] = useState('');
  const [editAmount, setEditAmount] = useState('');

  const [importModalOn, setImportModalOn] = useState(false);

  const [filterModalOn, setFilterModalOn] = useState(false);
  const [filterFromDate, setFilterFromDate] = useState('');
  const [filterToDate, setFilterToDate] = useState('');
  const [filterDescription, setFilterDescription] = useState('');
  const [filterHeaders, setFilterHeaders] = useState([]);
  const [filterFromAmount, setFilterFromAmount] = useState(0);
  const [filterToAmount, setFilterToAmount] = useState(0);

  const [changeViewModalOn, setChangeViewModalOn] = useState(false);

  //Hold the data for a new header
  const [addHeaderModalOn, setAddHeaderModalOn] = useState(false);
  const [newHeaderName, setNewHeaderName] = useState('');
  const [newHeaderIsIncome, setNewHeaderIsIncome] = useState(undefined);

  const [editHeaderModalOn, setEditHeaderModalOn] = useState(false);
  const [editHeader_Id, setEditHeader_Id] = useState('');
  const [editHeaderName, setEditHeaderName] = useState('');
  const [editHeaderIsIncome, setEditHeaderIsIncome] = useState(undefined);

  //Hold the data for transactions on page
  const [maxItemsOnPage, setMaxItemsOnPage] = useState(50);
 
  //Initialize the correct month and hold the data for the selected time period
  const daysInMonth = [31, isLeapYear(new Date().getFullYear()) ? 29 : 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
  const [fromDate, setFromDate] = useState(`${new Date().getFullYear()}-${String(new Date().getMonth() + 1).padStart(2, '0')}-01`)
  const [toDate, setToDate] = useState(`${new Date().getFullYear()}-${String(new Date().getMonth() + 1).padStart(2, '0')}-${daysInMonth[new Date().getMonth()]}`);

  //View option for the overview section
  const [view, setView] = useState('table');

  //Options
  const [lang, setLang] = useState('en');
  const [showEmpty, setShowEmpty] = useState(true);

  //Error modal
  const [errorModalOn, setErrorModalOn] = useState('');

  let history = useHistory();
  
  //Change the language
  function handleLangChange(e) {
    setLang(e.target.value);
}

  function logout() {
    setUserId(prev => 0);
    setEmail(prev => '');
    setPassword(prev => '');
    setTransactions(prev => []);
    setHeaders(prev => []);
    history.push('/login')
  }

   const inputField = useRef();
  
  //Read the data from a file selected by the user
  async function importTransactions(e) {

  try {
      let file = inputField.current.files[0];
      inputField.current.value = null;
      let textResponse = await file.text();
      let lines = textResponse.split('\n');
      for (let i = 1; i < lines.length - 1; i++) {
          let fields = lines[i].split(';');
          let date = `${fields[0].substring(1, 5)}-${fields[0].substring(5, 7)}-${fields[0].substring(7, 9)}`
          let description = fields[1].substring(1, fields[1].length - 1)
          let headerId = fields[5] === '"Bij"' ? headers.find(header => header.name === 'Unallocated income').id : headers.find(header => header.name === 'Unallocated expenses').id;
          let amount = Number(fields[6].substring(1, fields[6].length - 1).replace(',','.'));
          addTransaction(date, description, headerId, amount)
      }
  } catch (e) {
      setErrorModalOn({en: 'File could not be read', nl: 'Bestand kan niet gelezen worden'})
  }
}

  //Add header if it's not already in the list. First letter should be uppercase with the rest lowercase.
  function addHeader(name, isIncome) {
    try {
      Server.addHeader(name, isIncome, userId, email, password)
      .then(res => setHeaders(prev => ([...res, ...prev])));
    }
    catch (err) {
      alert(lang === 'en' ? 'Unauthorized' : 'Niet geautoriseerd');
    }
  }

  //Remove header if it is not a default header and if there are no more transactions with that header
  function removeHeader(headerId) {
    for (let transaction of transactions) {
      if (transaction.header_id === headerId) {
         setErrorModalOn({en: 'There are still transactions with that header', nl: 'Er zijn nog transacties in die rubriek'});
        return;
      }
    }
    try {
      Server.deleteHeader(headerId, userId, email, password).then(res => res.status === 204 && setHeaders(prev => prev.filter(el => el.id !== headerId)));   
    }
    catch (err) {
      setErrorModalOn({en: 'Cannot delete header', nl: 'Kan rubriek niet verwijderen'});
    }
  }

  function changeHeader(headerId, name) {
    try {
      Server.changeHeader(headerId, name.trim(), userId, email, password)
      .then(res => {
        const index = headers.findIndex(el => el.id === headerId);
         setHeaders(prev => [...prev.slice(0, index), ...res, ...prev.slice(index + 1, prev.length)]);
       })
    }
    catch (err) {
      setErrorModalOn({en: 'Cannot change header', nl: 'Kan rubriek niet wijzigen'});
     }
  }

  //Add a transaction to the list
  function addTransaction(date, description, headerId, amount) {
     try {
      Server.addTransaction(date, description, headerId, amount, userId, email, password)
      .then(res => {
        setTransactions(prev => (sortByDateThenId([...res, ...prev])))
        //Reset the data fields
        setDescription('');
        setHeaderId('');
        setAmount('');
      });
    }
    catch (err) {
      setErrorModalOn({en: 'Cannot add transaction', nl: 'Kan transactie niet toevoegen'});
    }

   }

  //Remove transaction from the list
  function removeTransaction(id) {
    try {
      Server.deleteTransaction(id, userId, email, password).then(res => res.status === 204 && setTransactions(prev => prev.filter(el => el.id !== id)));
    }
    catch (err) {
      setErrorModalOn({en: 'Cannot delete transaction', nl: 'Kan transactie niet verwijderen'});
    }
   }

  //Change transaction
  function changeTransaction(id, date, description, headerId, amount) {
   try {
      Server.changeTransaction(id, date, description.trim(), headerId, amount, userId, email, password)
      .then(res => {
        const index = transactions.findIndex(el => el.id === id);
         setTransactions(prev => sortByDateThenId([...prev.slice(0, index), ...res, ...prev.slice(index + 1, prev.length)]));
         })
    }
    catch (err) {
      setErrorModalOn({en: 'Cannot change transaction', nl: 'Kan transactie niet wijzigen'});
    }
 }

  function showCorrectHeader(headerId) {
    let headerName = headers.find(el => el.id === headerId).name;
    if (lang === 'nl' && headerName === 'Unallocated income') {
      return 'Ongerubriceerde inkomsten';
    }
    else if (lang === 'nl' && headerName === 'Unallocated expenses') {
      return 'Ongerubriceerde uitgaven';
    }
    else  {
      return headerName;
    }
  }

  function sortByDateThenId(array) {
    return array.sort((a, b) => {
      if (a.date > b.date) {
        return -1;
      }
      else if (a.date < b.date) {
        return 1;
      }
      else {
        if (a.id > b.id) {
          return -1
        }
        else {
          return 1;
        }
        }
    })
  }

  //Check whether the current year is a leap year
  function isLeapYear(year) {
    return year % 400 === 0 || (year % 4 === 0 && year % 100 !== 0)
  }

  //Reset the filter
  function resetFilter() {
    setFilterFromDate('');
    setFilterToDate('');
    setFilterDescription('');
    setFilterHeaders([]);
    setFilterFromAmount(0);
    setFilterToAmount(0);
}

let filteredTransactions = useRef();
filteredTransactions.current = transactions;

if (filterFromDate) {
    filteredTransactions.current = filteredTransactions.current.filter(el => el.date >= filterFromDate);
}

if (filterToDate) {
    filteredTransactions.current = filteredTransactions.current.filter(el => el.date <= filterToDate);
}

if (filterDescription) {
    filteredTransactions.current = filteredTransactions.current.filter(el => RegExp(filterDescription, 'gi').test(el.description));
}

if (filterHeaders.length) {
    filteredTransactions.current = filteredTransactions.current.filter(el => filterHeaders.includes(String(el.header_id)));
}

if (filterFromAmount) {
    filteredTransactions.current = filteredTransactions.current.filter(el => Number(el.amount) >= filterFromAmount);
}
if (filterToAmount) {
    filteredTransactions.current = filteredTransactions.current.filter(el => Number(el.amount) <= filterToAmount);
}

  useEffect(() => {
    if (!userId) {
      return;
    }
    let promises = [Server.getHeaders(userId, email, password), Server.getTransactions(userId, email, password)];
    try {
      Promise.all(promises).then(res => {
        setHeaders(prev => [...res[0]]);
        setTransactions(prev => [...sortByDateThenId(res[1])]);
      });
    }
    catch (err) {
      setErrorModalOn({en: 'Cannot load data', nl: 'Kan gegevens niet laden'});
    }
   }, [userId, email, password, lang]);

  return (
    <div className={styles.app}>

      {errorModalOn && <ErrorModal
      lang={lang}
      errorModalOn={errorModalOn}
      setErrorModalOn={setErrorModalOn}/>}
      
      {addTransactionModalOn && <AddEditTransactionModal
      addEdit={'add'}
      lang={lang}
      setModal={setAddTransactionModalOn}
      date={date}
      description={description}
      headerId={headerId}
      amount={amount}
      setDate={setDate}
      setDescription={setDescription}
      setHeaderId={setHeaderId}
      setAmount={setAmount}
      headers={headers}
      showCorrectHeader={showCorrectHeader}
      addTransaction={addTransaction}
      setErrorModalOn={setErrorModalOn}/>}

      {importModalOn && <ImportModal
      lang={lang}
      inputField={inputField}
      importTransactions={importTransactions}
      setImportModalOn={setImportModalOn}
      headers={headers} 
      showCorrectHeader={showCorrectHeader} 
      filterFromDate={filterFromDate} 
      setFilterFromDate={setFilterFromDate} 
      filterToDate={filterToDate} 
      setFilterToDate={setFilterToDate} 
      filterDescription={filterDescription} 
      setFilterDescription={setFilterDescription} 
      setFilterHeaders={setFilterHeaders} 
      filterFromAmount={filterFromAmount} 
      setFilterFromAmount={setFilterFromAmount} 
      filterToAmount={filterToAmount} 
      setFilterToAmount={setFilterToAmount} />}

      {filterModalOn && <FilterModal
      lang={lang}
      setFilterModal={setFilterModalOn}
      headers={headers} 
      showCorrectHeader={showCorrectHeader} 
      filterFromDate={filterFromDate} 
      setFilterFromDate={setFilterFromDate} 
      filterToDate={filterToDate} 
      setFilterToDate={setFilterToDate} 
      filterDescription={filterDescription} 
      setFilterDescription={setFilterDescription} 
      setFilterHeaders={setFilterHeaders} 
      filterFromAmount={filterFromAmount} 
      setFilterFromAmount={setFilterFromAmount} 
      filterToAmount={filterToAmount} 
      setFilterToAmount={setFilterToAmount}/>}

      {editTransactionModalOn && <AddEditTransactionModal
      addEdit='edit'
      lang={lang}
      setModal={setEditTransactionModalOn}
      id={editTransactionId}
      date={editDate}
      description={editDescription}
      headerId={editHeaderId}
      amount={editAmount}
      setDate={setEditDate}
      setDescription={setEditDescription}
      setHeaderId={setEditHeaderId}
      setAmount={setEditAmount}
      headers={headers}
      showCorrectHeader={showCorrectHeader}
      changeTransaction={changeTransaction}
      setErrorModalOn={setErrorModalOn}/>}

      {changeViewModalOn && <ChangeViewModal 
      lang={lang}
      setChangeViewModalOn={setChangeViewModalOn}
      fromDate={fromDate}
      setFromDate={setFromDate}
      toDate={toDate}
      setToDate={setToDate}
      showEmpty={showEmpty}
      setShowEmpty={setShowEmpty}
      setErrorModalOn={setErrorModalOn}/>}

      {addHeaderModalOn && <AddEditHeaderModal
      lang={lang}
      addEdit={'add'}
      setModal={setAddHeaderModalOn}
      headers={headers}
      name={newHeaderName}
      setName={setNewHeaderName}
      isIncome={newHeaderIsIncome}
      setIsIncome={setNewHeaderIsIncome}
      addHeader={addHeader}
      setErrorModalOn={setErrorModalOn}/>}

      {editHeaderModalOn && <AddEditHeaderModal 
      lang={lang}
      addEdit={'edit'}
      setModal={setEditHeaderModalOn}
      headers={headers}
      id={editHeader_Id}
      name={editHeaderName}
      setName={setEditHeaderName}
      isIncome={editHeaderIsIncome}
      setIsIncome={setEditHeaderIsIncome}
      changeHeader={changeHeader}
      setErrorModalOn={setErrorModalOn}/>}

      {userId > 0 && <nav>
        <div className={styles.navigation}>
          <h2>XPENSOFT</h2>
          <NavLink data-test='navlink-transactions' to='/transactions' activeClassName={styles.active}>
            {lang === 'en' ? 'Transactions' : 'Transacties'}
          </NavLink>
          <NavLink to='/overview' activeClassName={styles.active}>
            {lang === 'en' ? 'Overview' : 'Overzicht'}
          </NavLink>
        </div>
         <div className={styles.langbar}>
            <input type='radio' id='en' name='lang' value='en' checked={lang === 'en' ? true : false} onChange={handleLangChange}/>
            <img src={imageEN} alt=''/>
            <input type='radio' id='nl' name='lang' value='nl' checked={lang === 'en' ? false : true} onChange={handleLangChange}/>
            <img src={imageNL} alt=''/>
           <button onClick={logout}>{lang === 'en' ? 'Logout' : 'Uitloggen'}</button>
        </div>
      </nav>}
      <main>
        <Switch>
          <Route exact path='/'>
            {userId && <Redirect to='transactions'/>}
            <Redirect to='/login' />
           </Route>
          <Route path='/login'>
            {userId && <Redirect to='transactions'/>}
            <Login
            setUserId={setUserId}
            email={email}
            setEmail={setEmail}
            password={password}
            setPassword={setPassword}
            newEmail={newEmail}
            setNewEmail={setNewEmail}
            newPassword1={newPassword1}
            setNewPassword1={setNewPassword1}
            newPassword2={newPassword2}
            setNewPassword2={setNewPassword2}
            forgotPasswordEmail={forgotPasswordEmail}
            setForgotPasswordEmail={setForgotPasswordEmail}
            lang={lang}
            handleLangChange={handleLangChange} />
          </Route>
          <Route path='/transactions'>
            <Transactions
            userId={userId}
            transactions={filteredTransactions.current} 
            headers={headers} 
            showCorrectHeader={showCorrectHeader} 
            removeTransaction={removeTransaction} 
            changeTransaction={changeTransaction} 
            lang={lang} 
            maxItemsOnPage={maxItemsOnPage} 
            setMaxItemsOnPage={setMaxItemsOnPage}
            setAddTransactionModalOn={setAddTransactionModalOn}
            setEditTransactionModalOn={setEditTransactionModalOn}
            setEditTransactionId={setEditTransactionId}
            setEditDate={setEditDate}
            setEditDescription={setEditDescription}
            setEditHeaderId={setEditHeaderId}
            setEditAmount={setEditAmount}
            setImportModalOn={setImportModalOn}
            setFilterModalOn={setFilterModalOn}
            resetFilters={resetFilter}/>
          </Route>
          <Route path='/overview'>
            <Overview 
            userId={userId}
            transactions={transactions} 
            headers={headers} 
            showCorrectHeader={showCorrectHeader} 
            fromDate={fromDate} 
            setFromDate={setFromDate} 
            toDate={toDate} 
            setToDate={setToDate} 
            addHeader={addHeader} 
            removeHeader={removeHeader} 
            changeHeader={changeHeader}
            showEmpty={showEmpty} 
            setShowEmpty={setShowEmpty} 
            view={view} 
            setView={setView} 
            setChangeViewModalOn={setChangeViewModalOn}
            setAddHeaderModalOn={setAddHeaderModalOn}
            setEditHeaderModalOn={setEditHeaderModalOn}
            setEditHeader_Id={setEditHeader_Id}
            setEditHeaderName={setEditHeaderName}
            setEditHeaderIsIncome={setEditHeaderIsIncome}
            lang={lang}/>
            </Route>
         </Switch>
      </main>
    </div>
  );
}

export default App;
